<template>
  <el-dialog
    :title="`${formatTitle(data.rule)}`"
    v-model="visibility"
    width="30%"
  >
    <div>
       {{ getTaskTypeFullName(data.taskType) }} 
    </div>
    <div v-if="data.rule==='time'">
       {{ data.startTime.toString() }} - {{ data.endTime.toString() }} 
    </div>
    <div v-if="data.rule==='frequency'">
      {{ formatTime(data.time) }}{{ data.frequency }}
    </div>
    <div v-if="data.rule==='order'">
       {{ getTaskTypeFullName(data.basedTaskType) }}{{ formatOrder(data.order) }}
    </div>
    <template #footer>
      <span class="dialog-footer">
        <el-button
          type="primary"
          @click="visibility = false"
        > </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { getTaskTypeFullName } from '@/utils/taskType'
import { VModel } from '@/utils'

export default {
  name: 'RulePopup',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object,
      required: true
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const visibility = VModel(props, ctx)
    console.log(props.data)
    const formatTitle = (type) => {
      switch (type) {
        case 'time':
          return ''
        case 'frequency':
          return ''
        case 'order':
          return ''
      }
    }

    const formatTime = (time) => {
      switch (time) {
        case 1:
          return ''
        case 2:
          return ''
        case 3:
          return ''
      }
    }

    const formatOrder = (order) => {
      if (order === 0) {
        return ''
      } else {
        return ''
      }
    }

    return {
      visibility,
      formatTitle,
      formatTime,
      formatOrder,
      getTaskTypeFullName
    }
  }
}
</script>

